// post compoent; displays the entire blog post
import { useParams } from "react-router";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import { useEffect, useRef } from "react";
import { posts } from "../../data/posts";
import { DiscussionEmbed } from "disqus-react";
import useObserver from "../../hooks/useObserver";
import DefaultImage from "../../components/utils/DefaultImage";
import { disqusShortname } from "../../data/data";

export default function BlogPost() {
	const { index } = useParams();
	const imageRef = useRef(null);
	const isImageVisible = useObserver(imageRef, { threshold: 0 });

	// update document title and scroll to top on page load
	useEffect(() => {
		document.title = posts[index - 1].title;
		window.scrollTo({ top: 0, behavior: "smooth" });
	}, [index]);

	return (
		<div className="animate__animated animate__fadeIn py-20">
			<header className="bg-gray-100 dark:bg-gray-800 p-7">
				<h3 className="text-2xl font-semibold text-gray-700 dark:text-white mb-0">
					{posts[index - 1].title}
				</h3>
				<span className="text-sm tracking-wide uppercase dark:text-gray-500">
					Posted on: {posts[index - 1].date}
				</span>
			</header>
			<div className="aspect-w-2 aspect-h-1 mb-10 relative" ref={imageRef}>
				{/* lazy load image */}
				{isImageVisible ? (
					<img
						src={`/images/posts/${index}.jpg`}
						alt="post"
						className="object-cover"
					/>
				) : (
					<DefaultImage />
				)}
			</div>
			<div className="mb-10">
				<ReactMarkdown
					children={posts[index - 1].body}
					remarkPlugins={[remarkGfm]}
				/>
			</div>
			<div>
				<h3>Comments</h3>
				{/* disqus comment */}
				<DiscussionEmbed
					shortname={disqusShortname}
					config={{
						url: window.location.href,
						identifier: index,
						title: posts[index - 1].title,
					}}
				/>
			</div>
		</div>
	);
}
